* {
	box-sizing: border-box;
}

body {
	margin: 0;
	max-width: 100vw;
	padding: 0;
	background-color: #e1e1e1;
}

main {
	width: 80%;
	margin-left: calc(20% + 30px);
	padding: 30px;
}

.category {
	margin-bottom: 40px;
}

.function {
	margin-bottom: 30px;
	background-color: white;
	padding: 20px;
	border-radius: 3px;
	box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.4);
}

.body__wrapper {
	display: flex;
}

h1, h2, h3, h4, h5, h6 {
	margin-top: 0;
}

.body-block {
	padding: 20px 0;
}